import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'main',
      component:resolve => require(['@/pages/main'],resolve),
    },
    {
      path: '/about',
      name: 'about',
      component:resolve => require(['@/pages/onepage/about'],resolve),
    },
    {
      path: '/newsList',
      name: 'newsList',
      component:resolve => require(['@/pages/news/newsList'],resolve),
    },
    {
      path: '/proList',
      name: 'proList',
      component:resolve => require(['@/pages/produce/proList'],resolve),
    },
    {
      path: '/recruit',
      name: 'recruit',
      component:resolve => require(['@/pages/onepage/recruit'],resolve),
    },
    {
      path: '/hello',
      name: 'hello',
      component:resolve => require(['@/pages/hello'],resolve),
    },
    {
      path: '/service',
      name: 'service',
      component:resolve => require(['@/pages/service/service'],resolve),
    },
    {
      path: '/caseList',
      name: 'caseList',
      component:resolve => require(['@/pages/case/caseList'],resolve),
    },
  ]
})



